import { SETSEARCHSTATUS, SETENVELOPS, SETCOGURI, SETCURRENTLAYER } from "./types";
import { colors } from "../../styles";


let dmin=0.00001
let dmax=85
let legend_colors=colors.suitability
let legend_title='Convenance'

const initialState = {
  features: [],
  fetching: false,
  cog_uri: "https://object-arbutus.cloud.computecanada.ca/bq-io/io/btsl-connectivity/NC_NC-HabitatSuitability-BLBR-it22-ts2010.tif",
  current_layer: {},
  dmin: 0.001,
  dmax: 85,
  legend_colors: colors.suitability,
  legend_title:'Convenance'
};



const mapReducer = (state = initialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case SETSEARCHSTATUS:
      return {
        ...state,
        fetching: payload.fetching,
      };
    case SETENVELOPS:
      return {
        ...state,
        features: payload.features,
        fetching: payload.fetching,
        summary: payload.summary,
      };
    case SETCOGURI:
      return {
        ...state,
        cog_uri: payload.cog_uri, 
        current_layer: payload.current_layer,
        dmin: payload.dmin,
        dmax: payload.dmax,
        legend_colors: payload.legend_colors,
        legend_title: payload.legend_title,
      };
    default:
      return state;
  }
};

export default mapReducer;

/**
 *
 * @param {*} status
 * @returns
 */
export const updateFetchingStatus = (status) => async (dispatch) => {
  dispatch({ type: SETSEARCHSTATUS, payload: { fetching: status } });
};

/**
 *
 * @param {*} status
 * @returns
 */
export const updateCOGURI = (scenario_landuse,scenario_climate,layer,species,year) => async (dispatch) => {
  const uri = "https://object-arbutus.cloud.computecanada.ca/bq-io/io/btsl-connectivity/"+scenario_landuse+"_"+scenario_climate+'-'+layer+'-'+species+'-it22-ts'+(parseInt(year)+2010)+".tif"
  if(layer=='curmap'){
    dmin=0.000025
    dmax=0.001
    legend_colors=colors.connectivity
    legend_title='Connectivité'
  }else if(layer=='HabitatSuitability'){
    dmin=0.00001
    dmax=85
    legend_colors=colors.suitability
    legend_title='Convenance'
  }
  dispatch({ type: SETCOGURI, payload: { cog_uri: uri , current_layer: layer, dmin: dmin, dmax: dmax, legend_colors: legend_colors, legend_title:legend_title} });
};
